import React from "react";
import Button from "../elements/Button";
import Fade from 'react-reveal/Fade';

function Nav(props) {
 return (
  <Fade top distance="20px">
   <nav className="navbar navbar-expand-lg navbar-light mt-4">
    <div className="container-fluid">
     <a className="navbar-brand" href="#">
      <img src={props.logo} alt="" height="50" />
     </a>
     <button
      className="navbar-toggler"
      type="button"
      data-bs-toggle="collapse"
      data-bs-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
     >
      <span className="navbar-toggler-icon"></span>
     </button>
     <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0 me-3">
       <li className="nav-item">
        <a className="nav-link link-top" href="#">
         Apps
        </a>
       </li>
       <li className="nav-item">
        <a className="nav-link link-top" href="#">
         Features
        </a>
       </li>
       <li className="nav-item">
        <a className="nav-link link-top" href="#">
         Support
        </a>
       </li>
      </ul>
      <form className="d-flex">
       <Button className="px-3" isBtnLogin>
        LOGIN
       </Button>
      </form>
     </div>
    </div>
   </nav>
  </Fade>
 );
}

export default Nav;
