import Section from "../elements/Section";
import CardRich from "../elements/CardRich";
import { FaTimes } from "react-icons/fa";
import { FaToggleOff } from "react-icons/fa";
import { FaTint } from "react-icons/fa";
import Fade from "react-reveal/Fade";

function Rich() {
 const times = <FaTimes></FaTimes>;
 const toggle = <FaToggleOff></FaToggleOff>;
 const thumb = <FaTint></FaTint>;
 return (
  <Section className="section-rich">
   <div className="container">
    <div className="row"></div>
    <div className="col pt-100">
     <Fade bottom distance="20px">
      <h2 id="text-rich" className="text-center">
       Feature Rich
      </h2>
     </Fade>
     <Fade bottom distance="20px" delay={400}>
      <p id="p-rich" className="mt-4 text-center">
       Everything you need to manage your finances. Keep track of all of your{" "}
       <br /> payments, invoices, expenses, etc. in one place with zero fees.
      </p>
     </Fade>
     <Fade left distance="20px" delay={800}>
      <div className="row row-rich">
       <CardRich title="example" icon={times}></CardRich>
       <CardRich title="testing" icon={toggle}></CardRich>
       <CardRich title="sample" icon={thumb}></CardRich>
      </div>
     </Fade>
    </div>
   </div>
  </Section>
 );
}

export default Rich;
