import Section from "../elements/Section";
import Mockup from "../assets/image/mockup.png";
import Fade from "react-reveal/Fade";

function Header() {
 return (
  <Section className="section-header">
   <div className="container">
    <header className="text-left head-top">
     <div className="row">
      <div className="col-lg-6">
       <Fade bottom distance="20px" delay={400}>
        <h1 id="header" className="text-white">
         Explore The Worlds, <br />
         As Easy on Click
        </h1>
       </Fade>
       <Fade bottom distance="20px" delay={800}>
        <p id="header-p" className="mt-4 text-white">
         You will see beautiful moment you <br />
         can't beyond your imagination
        </p>
       </Fade>
       <Fade bottom distance="20px" delay={1200}>
        <a href="" className="btn btn-get-started">
         get started
        </a>
       </Fade>
      </div>
      <div className="col-lg-6">
       <Fade right distance="20px" delay={800}>
       <img className="app-img" src={Mockup} alt="mockup" />
       </Fade>
      </div>
     </div>
    </header>
   </div>
  </Section>
 );
}

export default Header;
