import PropTypes from "prop-types";

function CardFeature(props) {
 return (
  <div className="col-lg-4 card-feature">
   <div className="card-icon ms-4">
    <i className="text-white">{props.icon}</i>
   </div>
   <div className="card-features">
    <h4>{props.title}</h4>
    <p className=" text-muted pt-3">
     That's right, completely free. There is no limited or trial version, only
     full. No hidden charges or setup fees. No counter. Free means free.
    </p>
   </div>
  </div>
 );
}

CardFeature.propTypes = {
 title: PropTypes.string,
};

export default CardFeature;
