import Section from "../elements/Section";
import Shape from "../assets/image/shape.png";
import Fade from "react-reveal/Fade";

function Docs() {
 return (
  <Section className="section-docs">
   <div className="container">
    <div className="row">
     <div className="col-12 col-lg-6 col-docs">
      <Fade bottom distance="20px">
       <h1 className="col-docs-h1">
        Explore The Worlds, <br />
        As Easy on Click
       </h1>
      </Fade>
      <Fade bottom distance="20px" delay={400}>
       <p className="mt-4 col-docs-p">
        You will see beautiful moment you <br />
        can't beyond your imagination
       </p>
      </Fade>
      <Fade bottom distance="20px" delay={800}>
       <a href="" className="btn btn-docs px-4">
        get started
       </a>
      </Fade>
     </div>
     <div className="col-12 col-lg-6 shape">
      <Fade bottom distance="20px" delay={400}>
       <img src={Shape} alt="" />
      </Fade>
     </div>
    </div>
   </div>
  </Section>
 );
}

export default Docs;
