import Section from "../elements/Section";
import CardFeature from "../elements/CardFeature";
import { FaTimes } from "react-icons/fa";
import { FaToggleOff } from "react-icons/fa";
import { FaTint } from "react-icons/fa";
import Fade from "react-reveal/Fade";

function Features() {
 const times = <FaTimes></FaTimes>;
 const toggle = <FaToggleOff></FaToggleOff>;
 const thumb = <FaTint></FaTint>;
 return (
  <Section className="section-features">
   <div className="container">
    <Fade right distance="20px" delay={1600}>
     <div className="row feature">
      <CardFeature title="free" icon={times}></CardFeature>
      <CardFeature title="online" icon={toggle}></CardFeature>
      <CardFeature title="Customized" icon={thumb}></CardFeature>
     </div>
    </Fade>
   </div>
  </Section>
 );
}

export default Features;
