import PropTypes from "prop-types";

function CardRich(props) {
 return (
  <div id="rich-1" className="col-lg-4">
   <div className="card" style={{ border: "none" }}>
    <div className="card-rich float-start">
    <i className="text-white">{props.icon}</i>
    </div>
    <div className="text-rich">
     <h5 className="ms-3">{props.title}</h5>
     <p className="text-muted ms-3 text-small">
      That's right, completely free. There is no limited or trial version, only
      full. No hidden charges or setup fees. No counter. Free means free.
     </p>
    </div>
   </div>
  </div>
 );
}

CardRich.propTypes = {
 title: PropTypes.string,
};

export default CardRich;
